import React, { useState } from "react";
import Axios from "axios";
import { Switch, Redirect } from "react-router-dom";
import LoginForm from "./components/LoginForm";
import RegisterForm from "./components/RegisterForm";
import Navbar from "./components/Navbar";
import NavbarBottom from "./components/NavbarBottom";
import Dashboard from "./components/dashboard";
import PrivateNavBar from "./components/PrivateNavBar";
import Settings from "./components/Settings";
import PasswordReset from "./components/PasswordReset";
import Profile from "./components/Profile";
import PublicRoute from "./components/PublicRoute";
import PrivateRoute from "./components/PrivateRoute";

function App() {
  const [pageStatus, setPageStatus] = useState("");
  const [error, setError] = useState("");
  const [user, setUser] = useState({ name: "", email: "" });
  const [isLogin, setIsLogin] = useState(false);

  const register = (details) => {
    Axios.post("/users", {
      username: details.name,
      email: details.email,
      password: details.password,
      password_2nd: details.password_2nd,
    }).then((response) => {
      if (response.data.message) {
        //setLoginStatus(response.data.message);
        setError(response.data.message);
      } else {
        setUser({
          name: details.name,
          email: details.email,
        });
        setError("");
        setPageStatus("Logged_in");
        setIsLogin(true);
      }
    });
  };

  const Login = (details) => {
    Axios.post("/login", {
      email: details.email,
      password: details.password,
    }).then((response) => {
      if (response.data.message) {
        //setLoginStatus(response.data.message);
        setError(response.data.message);
      } else {
        setError("");
        setPageStatus("Logged_in");
        setIsLogin(true);
        setUser({
          name: details.name,
          email: details.email,
        });
        console.log(isLogin);
      }
    });
  };

  const Logout = (details) => {
    setUser({ name: "", email: "" });
    setError("");
    setPageStatus("");
    setIsLogin(false);
  };

  function refreshPage() {
    window.location.reload(false);
  }
  return (
    <>
      <Navbar setPageStatus={setPageStatus} pageStatus={pageStatus} />
      {pageStatus === "Logged_in" ? <Redirect to="/" /> : ""}
      <Switch>
        <PublicRoute restricted={false} exact path="/">
          <div className="welcome App">
            <h2>
              Welcome, <span> {user.name} </span>
              <button onClick={Logout}> Logout </button>
            </h2>
          </div>
        </PublicRoute>
        <PublicRoute restricted={true} exact path="/login">
          <div>
            <LoginForm Login={Login} error={error} setError={setError} />
          </div>
        </PublicRoute>
        <PublicRoute restricted={true} exact path="/signup">
          <div>
            <RegisterForm
              register={register}
              error={error}
              setError={setError}
            />
          </div>
        </PublicRoute>
        <PrivateRoute path="/settings">
          <PrivateNavBar />
          <Settings />
        </PrivateRoute>
        <PrivateRoute path="/passwordreset">
          <PasswordReset />
        </PrivateRoute>
        <PrivateRoute path="/profile">
          <Profile />
        </PrivateRoute>
        <PrivateRoute path="/about.html" component={refreshPage}></PrivateRoute>
        <PrivateRoute path="/dashboard" component={Dashboard}></PrivateRoute>
      </Switch>
      <NavbarBottom />
    </>
  );
}

export default App;
